ght: normal;\n    font-style: normal;\n    font-display: swap;\n}\n\n@font-face {\n    font-family: 'Raleway';\n    src: url('fonts/Raleway-Regular.woff2') format('woff2'),\n        url('fonts/Raleway-Regular.woff') format('woff');\n    font-weight: normal;\n    font-style: normal;\n    font-display: swap;\n}\n\n/* Normalize/Reset styles */\n\n:root {\n  line-height: 1.5;\n  --mauve-shadow: #744253;\n  --soft-linen: #ede7d9;\n  --evergreen: #041b15;\n  --rosewood: #9b596f;\n  --dusty-mauve: #ad7185;\n  --sand-dune: #ded3ba;\n}\n\n*,\n::before,\n::after {\n\tbox-sizing: border-box;\n}\n\nbody {\n\tmargin: 0;\n}\n\nh1, h2, h3, p, li, ul {\n  margin: 0;\n}\n\nul {\n  list-style-type: none;\n  padding-left: 0;\n}\n\nbutton,\ninput,\ntextarea {\n\tfont-family: inherit;\n\tfont-size: 100%;\n\tline-height: 1.15;\n  padding: 0;\n}\n\n/* Layout */\n\n#container {\n  display: flex;\n  height: 100vh;\n}\n\nh1, h2, h3 {\n  font-family: \"Quicksand\", system-ui;\n}\n\np, li, button, label, input {\n  font-family: \"Raleway\", system-ui;\n}\n\n/* Sidebar */\n\n#sidebar {\n  min-height: 100vh;\n  display: flex;\n  flex-direction: column;\n  flex-grow: 1/2;\n  flex-basis: 20vw;\n  background-color: var(--mauve-shadow);\n  padding: 1rem 1.5rem;\n}\n\n#sidebar-projects {\n  display: flex;\n  flex-direction: column;\n  overflow: hidden;\n}\n\n#project-list {\n  overflow-y: auto;\n}\n\n#sidebar li, #sidebar h3, #sidebar h2, #sidebar h1 {\n  color: white;\n}\n\n#project-head-div {\n  display: flex;\n  padding: 0.5rem 1rem;\n  margin: 1rem 0;\n}\n\n#project-head-div > h2 {\n  margin-right: auto;\n}\n\n.project-list-selected {\n  background-color: var(--dusty-mauve);\n  border-radius: 10px;\n}\n\n#project-head-div {\n  align-items: center;\n  cursor: pointer;\n}\n\n#project-add-button {\n  align-self: center;\n  background-color: var(--rosewood);\n  border-radius: 50%;\n  height: 50px;\n  width: 50px;\n  color: white;\n  font-size: 2rem;\n  border: none;\n}\n\n#project-add-button:hover {\n  background-color: #5b343e;\n  transform: translateY(2px);\n  cursor: pointer;\n}\n\n#project-add-button:focus {\n  outline: 2px solid var(--evergreen);\n}\n\n.project-item {\n  display: flex;\n  justify-content: space-between;\n  padding: 0.5rem 1rem;\n  border-radius: 10px;\n}\n\n.project-item:hover {\n  background-color: var(--dusty-mauve);\n  cursor: pointer;\n}\n\n.project-item img:hover {\n  transform: translateY(2px);\n  cursor: pointer;\n}\n\n#active-project-sidebar {\n  background-color: var(--rosewood);\n}\n\n#active-project-sidebar:hover {\n  background-color: #5b343e;\n}\n\n/* Content */\n\n#content {\n  flex: 1;\n  background-color: #ede7d9;\n  padding: 1rem 1.5rem;\n  box-shadow: inset 3px 0px 5px grey;\n  overflow-y: auto;\n}\n\n#project-todos-head {\n  display: flex;\n  justify-content: space-between;\n}\n\n#project-todos-head button {\n  padding: 0.5rem;\n  border-radius: 10px;\n  background-color: var(--mauve-shadow);\n  color: white;\n  border: 1px solid var(--evergreen);\n}\n\n#project-todos-head button:hover {\n  background-color: var(--dusty-mauve);\n  transform: translateY(1px);\n  cursor: pointer;\n}\n\n.todo-item {\n  display: grid;\n  grid-template-columns: 20px 3fr 1fr 100px;\n  align-items: center;\n  gap: 20px;\n\n  padding: 0.5rem 0 0.5rem 0.5rem;\n\n  border-bottom: 1px solid grey;\n}\n\n.todo-item:first-of-type {\n  margin-top: 0.75rem;\n}\n\n.todo-item:last-of-type {\n  border-bottom: none;\n}\n\n.todo-item:hover {\n  background-color: var(--sand-dune);\n  border-color: var(--sand-dune);\n  border-radius: 10px;\n  cursor: text;\n}\n\n.todo-item-icons {\n  grid-area: 1 / 4 / 2 / 5;\n  justify-self: end;\n}\n\n.todo-item-edit:hover, .todo-item-delete:hover {\n  transform: translateY(2px);\n  cursor: pointer;\n}\n\n/* Single Todo display */\n\n#single-todo {\n  display: grid;\n  gap: 20px;\n  grid-template-columns: 2fr 1fr;\n  grid-template-rows: 25px 1fr 4fr 2fr;\n}\n\n#single-todo-date {\n  grid-area: 1 / 1 / 2 / 2;\n}\n\n#single-todo-prio {\n  grid-area: 1 / 2 / 2 / 3;\n  justify-self: end;\n  position: relative;\n}\n\n.single-todo-low::before, .single-todo-medium::before, .single-todo-high::before {\n  content: \"\";\n  position: absolute;\n  left: -1.5rem;\n  height: 20px;\n  width: 20px;\n}\n\n.single-todo-low::before {\n  background: url(\"./icons/flag_low.svg\") no-repeat center center/cover;\n}\n\n.single-todo-low {\n  color: #3f7cac;\n}\n\n.single-todo-medium::before {\n  background: url(\"./icons/flag_medium.svg\") no-repeat center center/cover;\n}\n\n.single-todo-medium {\n  color: #f0a202;\n}\n\n.single-todo-high::before {\n  background: url(\"./icons/flag_high.svg\") no-repeat center center/cover;\n}\n\n.single-todo-high {\n  color: #fe4a49;\n}\n\n#single-todo-title {\n  grid-area: 2 / 1 / 3 / 2;\n}\n\n#single-todo-desc {\n  grid-area: 3 / 1 / 4 / 3;\n}\n\n#single-todo-notes {\n  grid-area: 4 / 1 / 5 / 3;\n}\n\n#single-todo-desc, #single-todo-notes {\n  background-color: var(--sand-dune);\n  padding: 0.5rem;\n  border-radius: 10px;\n}\n\n/* Projects overview display */\n\n#project-view {\n  display: grid;\n  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));\n  gap: 20px;\n}\n\n.project-card {\n  background-color: var(--sand-dune);\n  padding: 1rem;\n  border-radius: 10px;\n  box-shadow: -2px 2px 5px rgb(170, 170, 170);\n}\n\n.project-card:hover {\n  background-color: #d1c29f;\n  transform: translateY(2px);\n  cursor: pointer;\n}\n\n.project-card h3 {\n  justify-self: center;\n  margin-bottom: 0.5rem;\n}\n\n.project-card ul {\n  border-top: 1px solid var(--evergreen);\n  padding-top: 1rem;\n}\n\n/* Complete task styling */\n\n.todo-item-prio > input {\n  display: grid;\n  place-content: center;\n  appearance: none;\n  margin: 0;\n  width: 1.15em;\n  height: 1.15em;\n  border: 0.15em solid grey;\n  border-radius: 5px;\n}\n\n.todo-item-prio > input:hover {\n  cursor: pointer;\n}\n\n.priority-low > input {\n  border-color: #3f7cac;\n}\n\n.priority-medium > input {\n  border-color: #f0a202;\n}\n\n.priority-high > input {\n  border-color: #fe4a49;\n}\n\n.todo-item-prio > input::before {\n  content: \"\";\n  width: 0.75em;\n  height: 0.75em;\n  transform: scale(0);\n  box-shadow: inset 1em 1em var(--evergreen);\n  clip-path: polygon(80% 0, 36% 57%, 18% 44%, 0% 68%, 36% 100%, 100% 15%);\n}\n\n.todo-item-prio > input:checked::before {\n  transform: scale(1);\n}\n\n.todo-complete {\n  text-decoration: line-through;\n  color: grey;\n}\n\n/* Add Dialogs */\n\n#todo-add-dialog, #project-add-dialog {\n  background-color: var(--mauve-shadow);\n  border: 2px solid var(--evergreen);\n  border-radius: 10px;\n  color: white;\n}\n\n#todo-add-dialog form, #project-add-dialog form {\n  display: grid;\n  gap: 10px;\n}\n\n#project-add-dialog label {\n  grid-area: 1 / 1 / 2 / 2;\n}\n\n#project-add-name {\n  grid-area: 2 / 1 / 3 / 4;\n}\n\n#close-btn {\n  grid-area: 3 / 2 / 4 / 3;\n}\n\n#confirm-btn {\n  grid-area: 3 / 3 / 4 / 4;\n}\n\n.todo-add-field {\n  display: grid;\n  grid-template-columns: 7.5rem 1fr;\n}\n\n.todo-add-field input, textarea, #project-add-name {\n  border-radius: 10px;\n  padding: 0.5rem;\n  border: 1px solid grey;\n}\n\ntextarea {\n  resize: none;\n}\n\n.todo-add-field input:hover, textarea:hover, #project-add-name:hover {\n  background-color: rgb(235, 235, 235);\n}\n\n.todo-add-field input:focus, textarea:focus, #project-add-name:focus {\n  outline: 1px solid var(--evergreen);\n}\n\n#todo-add-dialog fieldset div {\n  display: grid;\n  grid-template-columns: 1em auto;\n  gap: 0.5em;\n}\n\n#todo-add-dialog input[type=\"radio\"] {\n  appearance: none;\n  margin: 0 0 1rem 0;\n  font: inherit;\n  color: white;\n  width: 1.15em;\n  height: 1.15em;\n  border: 0.15em solid white;\n  border-radius: 50%;\n  transform: translateY(0.2em);\n  display: grid;\n  place-content: center;\n}\n\n#todo-add-dialog input[type=\"radio\"]::before {\n  content: \"\";\n  width: 0.65em;\n  height: 0.65em;\n  border-radius: 50%;\n  transform: scale(0);\n  transition: 120ms transform ease-in-out;\n}\n\n#todo-priority-none::before {\n  box-shadow: inset 1em 1em white;\n}\n\n#todo-priority-low::before {\n  box-shadow: inset 1em 1em #3f7cac;\n}\n\n#todo-priority-medium::before {\n  box-shadow: inset 1em 1em #f0a202;\n}\n\n#todo-priority-high::before {\n  box-shadow: inset 1em 1em #fe4a49;\n}\n\n#todo-add-dialog input[type=\"radio\"]:hover {\n  cursor: pointer;\n}\n\n#todo-add-dialog input[type=\"radio\"]:checked::before {\n  transform: scale(1);\n}\n\n#todo-add-dialog input[type=\"radio\"]:focus {\n  outline: max(2px, 0.15em) solid white;\n  outline-offset: max(2px, 0.15em);\n}\n\n#todo-add-buttons {\n  justify-self: end;\n}\n\n#todo-add-buttons button, #close-btn, #confirm-btn {\n  background-color: var(--soft-linen);\n  color: var(--evergreen);\n  padding: 0.5rem;\n  border-radius: 10px;\n  border: 1px solid grey;\n}\n\n#todo-add-buttons button:hover, #close-btn:hover, #confirm-btn:hover {\n  background-color: var(--sand-dune);\n  cursor: pointer;\n}\n\n#todo-add-buttons button:focus, #close-btn:focus, #confirm-btn:focus {\n  outline: 1px solid var(--evergreen);\n}"],"sourceRoot":""}]);
